@import url(https://fonts.googleapis.com/css2?family=Questrial&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #2e2d59;
  align-items: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.YourTodo{
        font-size: 57px;
        text-align: center;
        margin: 0;
        padding-top: 40px;
        font-family:  'Questrial', sans-serif;
        background: -webkit-linear-gradient(#ffffff,#F2D5DB, #403E8C);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        color: #ce5e75;
}
.titleCounter {
        font-size: 24px;
        text-align: center;
        margin: 0;
        padding: 40px;
        font-family:  'Questrial', sans-serif;
        color: #F2D5DB;
}
.TodoSearch {
    background: #2E2D59;
    border-radius: 15px;
    border: 2px solid #65648C;
    margin: 0 24px;
    height: 40px;
    width: 85%;
    font-size: 18px;
    text-align: center;
    font-family: 'Questrial', sans-serif;
    font-weight: 400;
    color: #f2f2f2;

    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  }
  
  .TodoSearch::-webkit-input-placeholder {
    color: #A5A5A5;
    font-family:  'Questrial', sans-serif;
    font-weight: 400;
  }
  
  .TodoSearch:-ms-input-placeholder {
    color: #A5A5A5;
    font-family:  'Questrial', sans-serif;
    font-weight: 400;
  }
  
  .TodoSearch::placeholder {
    color: #A5A5A5;
    font-family:  'Questrial', sans-serif;
    font-weight: 400;
  }
  
  .TodoSearch:focus {
    outline-color: #F2D5DB;
  }
ul {
  margin: 0;
  padding: 0 0 56px 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.TodoItem {
    background-color: #2E2D59;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 5px 5px 20px #141425;
    width: 85%;
    border-radius: 20px;
  
  }
  
  .TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 20px;
    line-height: 24px;
    font-weight: 400;
    font-family:  'Questrial', sans-serif;
    color:#F2D5DB;
  }
  .TodoItem-p--complete {
    text-decoration: line-through;
  }
  
  .Icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
    /* background-color: #CCC; */
  }
  
  .Icon-check {
    position: absolute;
    left: 12px;
    color:#F2D5DB;
  }
  .Icon-check--active {
    color: #4caf50;
  }
  
  .Icon-delete {
    position: absolute;
    top: -24px;
    right: 0;
    color:#F2D5DB;
  }
  .Icon-delete:hover {
    color: #403E8C;
  }
.CreateTodoButton {
    background-color: #F2D5DB;
    box-shadow: 0px 3px 12px #65648C;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: #2E2D59;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;
  
    -webkit-transform: rotate(0);
  
            transform: rotate(0);
    transition: .3s ease;
    z-index: 1;
  }
  
  .CreateTodoButton:hover {
    -webkit-transform: rotate(224deg);
            transform: rotate(224deg);
  }
  .CreateTodoButton img {
    width: 34px;
  }
.ModalBackground {
    background: rgba(32,35,41,.8);
    position: fixed;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
  }
form {
    width: 90%;
    max-width: 300px;
    background-color: #fff;
    padding: 33px 40px;
    display: flex;
    align-content: center;
    flex-direction: column;
  }
  
  label {
    text-align: center;
    font-weight: bold;
    font-size: #1E1E1F;
    margin-bottom: 26px;
  }
  
  textarea {
    background-color: #F9FBFC;
    border: 2px, solid #202329;
    border-radius: 2px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: #1E1E1F;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100%-25px);
  }
  
  textarea::-webkit-input-placeholder {
  color: #a5a5a5;
  font-family: 'Montserrat';
  font-weight: 400;
  }
  
  textarea:-ms-input-placeholder {
  color: #a5a5a5;
  font-family: 'Montserrat';
  font-weight: 400;
  }
  
  textarea::placeholder {
  color: #a5a5a5;
  font-family: 'Montserrat';
  font-weight: 400;
  }
  
  textarea:focus {
    outline-color: #61DAFA;
  }
  
  .TodoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  
  .TodoForm-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
    font-family: 'Montserrat';
  }
  
  .TodoForm-button-add {
    background-color: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
  }
  
  .TodoForm-button-cancel {
    background: transparent;
  }
  
